const stripExtraSymbols = (str) => str.replace(/(^.*?=|")/gm, '').trim();

export const getCleanedCertData = (certificates) => {
    return certificates.map(cert => {
        let fields = cert.subjectName.split(",");

        // CN - common name, SN - surname, G/GN - given name
        const tags = ["CN=", "SN=", "G=", "GN="];
        let usefulFields = fields.filter(field =>
            (tags.indexOf(field.trim().slice(0, 2)) !== -1) ||
            (tags.indexOf(field.trim().slice(0, 3)) !== -1)
        );

        let certLabel = [];
        usefulFields.map(field =>
            certLabel.push(stripExtraSymbols(field)));

        let validTo = new Date(cert.validTo);
        validTo = validTo.toLocaleDateString('ru-RU');
        certLabel.push(` действителен до: ${validTo}`);
        certLabel = certLabel.join(", ");

        return { value: cert.thumbprint, text: certLabel };
    });
};
