import Vue from 'vue';
import {
    createHashSignature,
    getHashedData,
    getUserCertificates,
    isValidSystemSetup,
    verifyHashSignature
} from 'crypto-pro';
import FileReader from '@tanker/file-reader';

export default {
    namespaced: true,
    state: {
        certs: [],
        cert: null,
        isValidSystemSetup: false,
        progress: 0,
        noPlugin: false
    },
    getters: {},
    mutations: {
        setCertificates: (state, certs) => Vue.set(state, 'certs', certs),
        setSystemSetup: (state, isValid) => Vue.set(state, 'isValidSystemSetup', isValid),
        useCert: (state, thumbprint) => {
            let cert = null;
            if (state.certs.length)
                cert = state.certs.find((cert) => cert.thumbprint === thumbprint)
            Vue.set(state, 'cert', cert)
        },
        setProgress: (state, progress) => Vue.set(state, 'progress', progress),
        setNoPlugin: (state) => state.noPlugin = true
    },
    actions: {
        async init(context) {
            let isValid = await isValidSystemSetup()
            if (isValid) {
                context.commit('setSystemSetup', isValid);
                let certs = await getUserCertificates(false, true);
                context.commit('setCertificates', certs);
                return certs;
            }
        },
        async getHashedData(context, payload) {
            let oHashedData = await getHashedData();
            if (payload.file) {
                let file = payload.file;
                let chunkSize = 3 * 1024 * 1024; //3mb
                let chunks = Math.ceil(file.size / chunkSize);
                let currentChunk = 0;

                const reader = new FileReader(file);
                let chunk;
                do {
                    chunk = await reader.readAsArrayBuffer(chunkSize)
                    let sBase64Data = btoa(new Uint8Array(chunk).reduce((data, byte) => (
                        data.push(String.fromCharCode(byte)), data), []).join('')
                    );
                    await oHashedData.Hash(sBase64Data);
                    context.commit('setProgress', Math.round((currentChunk / chunks) * 100));
                    currentChunk++;
                } while (currentChunk < chunks);
                context.commit('setProgress', 100);
            }

            return oHashedData;
        },
        async signHash(context, payload) {
            let oHashedData = payload.hashedData;
            let cert = payload.cert;

            if (oHashedData && cert) {
                return createHashSignature(cert, oHashedData);
            }
        },
        async verifySign(context, payload) {
            let oHashedData = await context.dispatch("getHashedData", { file: payload.file })
            const reader = new FileReader(payload.sign);
            let signMessage = await reader.readAsText();

            if (oHashedData && signMessage) {
                return await verifyHashSignature(oHashedData, signMessage);
            }
        }

    }
}
