import Vue from 'vue'
import VueRouter from 'vue-router'

import Layout from '@/views/Layout'
import Verify from "@/components/content/Verify";
import Sign from "@/components/content/Sign";
import Decrypt from "@/components/content/Decrypt";
import Encrypt from "@/components/content/Encrypt";
import Plugin from "@/components/content/Plugin";

Vue.use(VueRouter)

const routes = [
    {
        path: "/",
        name: "Layout",
        component: Layout,
        children: [
            {
                path: "/verify",
                name: "Verify",
                component: Verify
            },
            {
                path: "/sign",
                name: "Sign",
                component: Sign
            },
            {
                path: "/decrypt",
                name: "Decrypt",
                component: Decrypt
            },{
                path: "/encrypt",
                name: "Encrypt",
                component: Encrypt
            },{
                path: "/plugin",
                name: "Plugin",
                component: Plugin
            },
            {
                path: "/*",
                redirect: Sign
            }
        ]
    },
    {
        path: "*",
        redirect: Sign
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
