<template>
  <b-container>
    <div class="verify-wrapper card">
      <h4>Подписание файла</h4>
      <form>
        <b-form-select
            :options="certSelectOptions"
            v-model="certFrom"
            @change="setCert"
            id="sign-select"
            class="custom-select-real"
        />
        <label
            class="custom-select-fake"
            for="sign-select">
          {{ certSelectLabel }}
        </label>
        <input
            type="file"
            class="custom-file-picker-real"
            id="file-picker"
            @input="setFile"
        />
        <label
            class="custom-file-picker-fake"
            for="file-picker">
          {{ filePickerLabel }}
        </label>
        <button
            type="button"
            class="form-button"
            @click="signDoc"
            :disabled="!certFrom || !signFile"
        >
          Подписать
        </button>
        <div v-if="errors" class="alert-warning">
          <span v-for="error in errors" :key="error">{{ error }}</span>
        </div>
      </form>
      <p>
        Стратегическое планирование имитирует CTR. Комплекс агрессивности,
        следовательно, органичен. Рекламный бриф масштабирует незначительный
        метод кластерного анализа. Эти слова совершенно справедливы,
        однако позиционирование на рынке синхронизирует инвариант.
      </p>
      <p>
        Психологическая среда, анализируя результаты рекламной кампании, обуславливает рейтинг.
        Флегматик, отбрасывая подробности, выстраивает миракль, не считаясь с затратами.
        Промоакция иллюстрирует продвигаемый имидж предприятия.
        Метод изучения рынка охватывает глубокий продуктовый ассортимент.
        Онтогенез, отбрасывая подробности, экономит реализм, именно об этом комплексе движущих сил
        писал З.Фрейд в теории сублимации.
      </p>
    </div>
    <p class="under-card">
      Стратегическое планирование имитирует CTR. Комплекс агрессивности,
      следовательно, органичен. Рекламный бриф масштабирует незначительный
      метод кластерного анализа. Эти слова совершенно справедливы,
      однако позиционирование на рынке синхронизирует инвариант.
    </p>
    <p class="under-card">
      Психологическая среда, анализируя результаты рекламной кампании, обуславливает рейтинг.
      Флегматик, отбрасывая подробности, выстраивает миракль, не считаясь с затратами.
      Промоакция иллюстрирует продвигаемый имидж предприятия.
      Метод изучения рынка охватывает глубокий продуктовый ассортимент.
      Онтогенез, отбрасывая подробности, экономит реализм, именно об этом комплексе движущих сил
      писал З.Фрейд в теории сублимации.
    </p>
  </b-container>
</template>

<script>
import {
  getCertificate,
  getSystemInfo
} from 'crypto-pro';
import { saveAs } from 'file-saver';
import { getCleanedCertData } from "@/utils";


export default {
  name: "Sign",
  data () {
    return {
      pluginInfo: null,
      signFile: null,
      value: 0,
      certFrom: "",
      valid: false,
      certSelectOptions: null,
      certSelectLabel: "Выберите сертификат получателя",
      filePickerLabel: "Выберите файл для подписания",
      errors: []
    }
  },
  computed: {
    isValidSystemSetup() {
      return this.$store.state.crypto.isValidSystemSetup;
    },
    certificates() {
      return this.$store.state.crypto.certs;
    },
    signCerts() {
      return this.$store.state.crypto.certs.filter((cert) => cert.hasPrivateKey)
    },
    progress() {
      return this.$store.state.crypto.progress;
    },
    cades() {
      return window.cadesplugin;
    }
  },
  methods: {
    addError (error) {
      this.errors.push(error);
    },
    setCertSelectOptions (signCerts) {
      this.certSelectOptions = getCleanedCertData (signCerts);
    },
    setFile (event) {
        this.signFile = event.target.files[0];
        this.filePickerLabel = this.signFile.name;
    },
    setCertSelectLabel () {
        this.certSelectLabel = this.certSelectOptions.
          find(cert => this.certFrom === cert.value).text;
    },
    setCert () {
      this.$store.commit('crypto/useCert', this.certFrom);
      this.setCertSelectLabel();
    },
    async signDoc() {

      if (this.signFile && this.certFrom) {
        let oHashData = await this.$store.dispatch("crypto/getHashedData", {file: this.signFile});
        let signCert = await getCertificate(this.certFrom);
        let signature = await this.$store.dispatch("crypto/signHash", {cert: signCert, hashedData: oHashData});

        let blob = new Blob([signature], { type: "text/plain;"});
        saveAs(blob, this.signFile.name + '.sgn');

      } else {
        this.addError("Выберите сертификат и файл для подписи");
      }

    }
  },
  mounted() {
    this.$store.dispatch('crypto/init')
        .then(() => {
          !this.signCerts
            ? this.addError("У Вас нет ни одного сертификата для подписи")
            : this.setCertSelectOptions(this.signCerts)
        })
        .catch(e => this.addError(e.message));

    getSystemInfo()
        .then((info) => {
          this.pluginInfo = info;
        })
        .catch(() => {
          this.addError("Для работы с сервисом необходимо установить плагин");
        })
  }
}
</script>
