<template>
  <b-container>
    <div class="verify-wrapper card">
      <h4>Шифрование файла</h4>
      <form>
        <b-form-select
            v-model="certTo"
            :options="certSelectOptions"
            class="custom-select-real"
            @change="setCertSelectLabel"
            id="custom-select"
        />
        <label
            class="custom-select-fake"
            for="custom-select">
          {{ certSelectLabel }}
        </label>
        <input
            type="file"
            class="custom-file-picker-real"
            id="file-picker"
            @input="setFile"

        />
        <label
            class="custom-file-picker-fake"
            for="file-picker">
          {{ filePickerLabel }}
        </label>
        <button
            type="button"
            class="form-button"
            @click="encryptDoc"
            :disabled="!encryptFile || !certTo"
        >
          Зашифровать файл
        </button>
        <div v-if="errors" class="alert-warning">
          <span v-for="error in errors" :key="error">{{ error }}</span>
        </div>
      </form>
      <p>
        Стратегическое планирование имитирует CTR. Комплекс агрессивности,
        следовательно, органичен. Рекламный бриф масштабирует незначительный
        метод кластерного анализа. Эти слова совершенно справедливы,
        однако позиционирование на рынке синхронизирует инвариант.
      </p>
      <p>
        Психологическая среда, анализируя результаты рекламной кампании, обуславливает рейтинг.
        Флегматик, отбрасывая подробности, выстраивает миракль, не считаясь с затратами.
        Промоакция иллюстрирует продвигаемый имидж предприятия.
        Метод изучения рынка охватывает глубокий продуктовый ассортимент.
        Онтогенез, отбрасывая подробности, экономит реализм, именно об этом комплексе движущих сил
        писал З.Фрейд в теории сублимации.
      </p>
    </div>
    <p class="under-card">
      Стратегическое планирование имитирует CTR. Комплекс агрессивности,
      следовательно, органичен. Рекламный бриф масштабирует незначительный
      метод кластерного анализа. Эти слова совершенно справедливы,
      однако позиционирование на рынке синхронизирует инвариант.
    </p>
    <p class="under-card">
      Психологическая среда, анализируя результаты рекламной кампании, обуславливает рейтинг.
      Флегматик, отбрасывая подробности, выстраивает миракль, не считаясь с затратами.
      Промоакция иллюстрирует продвигаемый имидж предприятия.
      Метод изучения рынка охватывает глубокий продуктовый ассортимент.
      Онтогенез, отбрасывая подробности, экономит реализм, именно об этом комплексе движущих сил
      писал З.Фрейд в теории сублимации.
    </p>
  </b-container>
</template>

<script>
import {
  getCertificate,
  getEnvelopedData,
  getSystemInfo
} from 'crypto-pro';
import FileReader from '@tanker/file-reader';
import { saveAs } from 'file-saver';
import { getCleanedCertData } from "@/utils";

export default {
  name: "Encrypt",
  computed: {
    isValidSystemSetup() {
      return this.$store.state.crypto.isValidSystemSetup;
    },
    certificates() {
      return this.$store.state.crypto.certs;
    },
    signCerts() {
      return this.$store.state.crypto.certs.filter((cert) => cert.hasPrivateKey)
    },
    cert() {
      return this.$store.state.crypto.cert;
    },
    progress() {
      return this.$store.state.crypto.progress;
    },
    cades() {
      return window.cadesplugin;
    }
  },
  data() {
    return {
      pluginInfo: null,
      encryptFile: null,
      value: 0,
      certTo: "",
      certSelectOptions: null,
      certSelectLabel: "Выберите сертификат получателя",
      filePickerLabel: "Выберите файл для расшифровки",
      errors: []
    }
  },
  mounted() {
    this.$store.dispatch('crypto/init')
        .then(() => {
          !this.certificates
              ? this.addError("У Вас нет ни одного сертификата")
              : this.setCertSelectOptions(this.certificates);
        })
        .catch(e => this.addError(e.message));

    getSystemInfo()
        .then((info) => {
          this.pluginInfo = info;
        })
        .catch(() => {
          this.addError("Для работы с сервисом необходимо установить плагин");
        })
  },
  methods: {
    addError (error) {
      this.errors.push(error);
    },
    setCertSelectOptions (certificates) {
      this.certSelectOptions = getCleanedCertData (certificates);
    },
    setCertSelectLabel () {
      if (this.certSelectOptions.length) {
        this.certSelectLabel = this.certSelectOptions.
        find(cert => this.certTo === cert.value).text;
      }
    },
    setFilePickerLabel () {
      this.filePickerLabel = this.encryptFile.name;
    },
    setFile (event) {
      this.encryptFile = event.target.files[0];
      this.filePickerLabel = this.encryptFile.name;
    },
    setCert () {
      this.$store.commit('crypto/useCert', this.certTo);
      this.setCertSelectLabel();
    },
    async encryptDoc() {
      if (this.encryptFile && this.certTo) {
        let file = this.encryptFile;

        let oEnvelopedData = await getEnvelopedData();
        let recipients = await oEnvelopedData.Recipients;
        await recipients.Clear();
        let encCert = await getCertificate(this.certTo);
        await recipients.Add(encCert._cadesCertificate);

        let chunkSize = 3 * 1024 * 1024;  // 3mb
        let chunks = Math.ceil(file.size / chunkSize);
        let currentChunk = 0;

        const reader = new FileReader(file);

        let chunk;

        do {
          chunk = await reader.readAsArrayBuffer(chunkSize);
          let sBase64Data = btoa(new Uint8Array(chunk).reduce((data, byte) => (
              data.push(String.fromCharCode(byte)), data), []).join('')
          );

          let end = currentChunk + 1 >= chunks;
          let encryptData = await oEnvelopedData.StreamEncrypt(sBase64Data, end);
          let sdBase64Data = Uint8Array.from(atob(encryptData), c => c.charCodeAt(0));
          let blob = new Blob([sdBase64Data], {type: "text/plain;"} );
          saveAs(blob, this.encryptFile.name);
          this.$store.commit('crypto/setProgress', Math.round((currentChunk / chunks) * 100));
          currentChunk++;
        } while (currentChunk < chunks);
      } else {
        this.addError("Выберите сертификат и файл для расшифровки");
      }

    },
  }
}
</script>
