<template>
  <b-container>
    <div class="verify-wrapper card">
      <h4>Проверка сертификата</h4>
      <form>
        <input
            type="file"
            id="file-picker-signature"
            class="custom-file-picker-real"
            @input="setVerifySign"
        />
        <label
            for="file-picker-signature"
            class="custom-file-picker-fake"
        >
          {{ verifySignLabel }}
        </label>
        <input
            type="file"
            id="file-picker-file"
            class="custom-file-picker-real"
            @input="setVerifyFile"
        />
        <label
            for="file-picker-file"
            class="custom-file-picker-fake"
        >
          {{ verifyFileLabel }}
        </label>
        <button
            type="button"
            @click="verifyDoc"
            class="form-button"
            :disabled="!verifySign || !verifySign"
        >
          Проверить файл
        </button>
        <div v-if="errors" class="alert-warning">
          <span v-for="error in errors" :key="error">{{ error }}</span>
        </div>
      </form>
      <p>
        Стратегическое планирование имитирует CTR. Комплекс агрессивности,
        следовательно, органичен. Рекламный бриф масштабирует незначительный
        метод кластерного анализа. Эти слова совершенно справедливы,
        однако позиционирование на рынке синхронизирует инвариант.
      </p>
      <p>
        Психологическая среда, анализируя результаты рекламной кампании, обуславливает рейтинг.
        Флегматик, отбрасывая подробности, выстраивает миракль, не считаясь с затратами.
        Промоакция иллюстрирует продвигаемый имидж предприятия.
        Метод изучения рынка охватывает глубокий продуктовый ассортимент.
        Онтогенез, отбрасывая подробности, экономит реализм, именно об этом комплексе движущих сил
        писал З.Фрейд в теории сублимации.
      </p>
    </div>
    <p class="under-card">
      Стратегическое планирование имитирует CTR. Комплекс агрессивности,
      следовательно, органичен. Рекламный бриф масштабирует незначительный
      метод кластерного анализа. Эти слова совершенно справедливы,
      однако позиционирование на рынке синхронизирует инвариант.
    </p>
    <p class="under-card">
      Психологическая среда, анализируя результаты рекламной кампании, обуславливает рейтинг.
      Флегматик, отбрасывая подробности, выстраивает миракль, не считаясь с затратами.
      Промоакция иллюстрирует продвигаемый имидж предприятия.
      Метод изучения рынка охватывает глубокий продуктовый ассортимент.
      Онтогенез, отбрасывая подробности, экономит реализм, именно об этом комплексе движущих сил
      писал З.Фрейд в теории сублимации.
    </p>
  </b-container>
</template>

<script>
import { getSystemInfo } from 'crypto-pro';

export default {
  name: "Verify",
  data() {
    return {
      pluginInfo: null,
      verifyFile: null,
      verifySign: null,
      sign: "",
      valid: false,
      verifySignLabel: "Выбрать файл c подписью",
      verifyFileLabel: "Выбрать файл c для проверки",
      errors: []
    }
  },
  computed: {
    isValidSystemSetup() {
      return this.$store.state.crypto.isValidSystemSetup;
    },
    certificates() {
      return this.$store.state.crypto.certs;
    },
    signCerts() {
      return this.$store.state.crypto.certs.filter((cert) => cert.hasPrivateKey)
    },
    cert() {
      return this.$store.state.crypto.cert;
    },
    progress() {
      return this.$store.state.crypto.progress;
    },
    cades() {
      return window.cadesplugin;
    }
  },
  mounted() {
    this.$store.dispatch('crypto/init')
        .then(() => {

        })
        .catch(e => this.addError(e.message));

    getSystemInfo()
        .then((info) => {
          this.pluginInfo = info;
        })
        .catch(() => {
          this.addError("Для работу с сервисом необходимо установить плагин");
        })
  },
  methods: {
    setVerifySign (event) {
      this.verifySign = event.target.files[0];
      this.verifySignLabel = this.verifySign.name;
    },
    setVerifyFile (event) {
      this.verifyFile = event.target.files[0];
      this.verifyFileLabel = this.verifyFile.name;
    },
    addError (error) {
      this.errors.push(error);
    },
    async verifyDoc() {
      if (this.verifyFile && this.verifySign) {
        this.valid = false
        this.$store.commit("crypto/setProgress", 0);
        let signature = await this.$store.dispatch("crypto/verifySign", {
          file: this.verifyFile, sign: this.verifySign, });
        this.valid = signature === true;
        this.valid
            ? this.addError("Подпись подтверждена")
            : this.addError("Подпись не подтверждена");

      } else {
        this.addError("Выберите подпись и файл для проверки");
      }

    }
  }
}
</script>
